import React, {useState, useEffect} from "react"
import Layout from "../components/functionalComponents/Layout"
import TextAnimation from "../components/functionalComponents/TextAnimation"
import Faseeh from "../images/Faseeh.png"
import "../components/styleSheets/global.css"
//deps
import {useMediaQuery} from "react-responsive"


const theme = {
  isSmall:false,
  primaryColor:"white",
  secondaryColor:"black"
}



const childrenStyle={
  display:"flex",
  justifyContent:"center",
  color:theme.secondaryColor,
  flexDirection:"column",
  alignContent:"center",
  alignItems:"center",
  margin:"5vh"
}

const IndexPage = () => {
  
  
  theme.isSmall=useMediaQuery({
    query:'(max-width:650px)'
  })

  
  const skills = [
    "human",
    "web developer",
    "react developer",
    "IoT enthusiast",
    "student",
    "website desiger"
  ]

  return (
    <Layout theme={theme} childrenStyle={childrenStyle}>
      <img src={Faseeh} alt="faseeh's picture"/>
      <div style={{marginTop:"2vh", width:"60vw", display:"flex", flexDirection:'column', justifyContent:'center', alignContent:'center', alignItems:'center', textAlign:"center"}}>
        <p style={{margin:".2em", fontSize:"1.5em"}}>hello, i am <strong>faseeh</strong></p>
        <p style={{margin:".2em"}}>i cofounded <strong>@pro.mirage</strong></p>
        <TextAnimation AnimatedArray={skills} childrenStyle={{margin:"0.2em"}}>i am a </TextAnimation>
        <p style={{margin:".2em"}}> 🚦 this website is under construction 🚦 </p>
      </div>
    </Layout>
  )
}

export default IndexPage
