import React, {useState, useEffect} from 'react'

export default function TextAnimation({children, AnimatedArray=["Set Value"], childrenStyle, AnimatedStyle}) {
    let index = 0;
    const [currentText, setCurrentText] = useState(AnimatedArray[index])
    const AnimatedArrayLength = AnimatedArray.length;
    


    const nextText = () => {

        setInterval(() => {
            if(index >= AnimatedArrayLength-1){
                index = 0;
            }else{
                index++;
            }
            
            let innerIndex = 0;
            let currentString = AnimatedArray[index];
            const writingInterval = setInterval(() => {
                if(innerIndex >= currentString.length){
                    clearInterval(writingInterval);
                }else{innerIndex++;}

                setCurrentText(currentString.substr(0,innerIndex));

            }, 50);

        }, 3500);
        
    }
    
    useEffect(nextText,[]);
    
    return (
        <p style={childrenStyle} >{children} <strong style={AnimatedStyle}>{currentText}</strong> </p> 
    )
}
